const app = getApp()
import helper from '../common/helper.js';
export default {
	data() {
		return {
			collectionList:[],
			imgUrl:''
		}
	},
	onLoad() {
		this.getList();
		this.imgUrl = helper.imgUrl
	},
	methods: {
		// collectionList
		getList(){
			console.log(app.globalData.token)
			uni.request({
				url: `${helper.requestUrl}/system-tools/apps`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					// uni.hideLoading();
					res = helper.null2str(res)
					console.log(res)
					if (res.data.status_code == 200) {
						this.collectionList = res.data.data
					} else {
						// uni.showToast({
						// 	title: res.data.message
						// });
					}
			
				}
			})
		},
		go(e){
			let type = e.currentTarget.dataset.type
			let extra = e.currentTarget.dataset.extra
			let id = e.currentTarget.dataset.id
			let name = e.currentTarget.dataset.name
			if(type == 'block'){
				uni.navigateTo({
					url:`/pages/${extra}`
				})
			} else if(type == 'series'){
				
			} else if(type == 'post'){
				uni.navigateTo({
					url:`/pages/articleDetail?id=${id}`
				})
				// 帖子
			} else if(type == 'child'){
				// 应用子
				console.log(id)
				uni.navigateTo({
					url:`/pages/applyShow?id=${id}`
				})
			} else if(type == 'ex_link'){
				console.log(extra,'+++++')
				if(extra == ''){
					uni.showToast({
						title: '此页面不存在',
						icon: 'none'
					})
				}else{







							// uni.navigateTo({
							// 	url:`/pages/iframe?url=${extra}&name=${name}`
							// })
							uni.showToast({
								title:'App'
							})

				}
				// plus.runtime.openURL(extra)
				// window.location.href = extra
				// 外联
			} else if(type == 'category'){
				uni.navigateTo({
					url:`/pages/applyShow?id=${id}`
				})
			}
			console.log(e)
		}
	}
}
